.switch {
    padding: 5px 7px;
    cursor: pointer;
   

   
    margin-top: 58px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.0);

  }

  .switch:active {
    animation: rotate 1.5s ease-in-out;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
