.social-links ul {
   /* Position it relative to the footer */
  bottom: 160px; /* Adjust this value based on how far from the bottom you want the social links */
  margin-left: -20px;
  transform: translateX(-50%, -50%); /* Center it horizontally */
  display: flex;
}

.social-links ul li {
  list-style: none;
}

.social-links ul li a {
  width: 50px;
  height: 50px;
  background-color: #198754;
  text-align: center;
  line-height: 80px;
  font-size: 24px;
  margin: 2px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;

  z-index: 1;
  transition: .5s;
}

.social-links ul li a:hover .icon {
  color: #198754;
  transform: rotateY(360deg);
}

.social-links ul li a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  
  transition: .5s;
  z-index: 2;
}

.social-links ul li a .icon {
  position: relative;
  color: #fff;
  transition: .5s;
  z-index: 3;
  bottom: 10px;
}

.social-links ul li a:hover:before {
  top: 0;
}

.social-links ul li:nth-child(1) a:before {
  background: #fff;
}

.social-links ul li:nth-child(2) a:before {
  background: #fff;
 
}

.social-links ul li:nth-child(3) a:before {
  background: #fff;
  
}

.social-links ul li:nth-child(4) a:before {
  background: #fff;
}
