}
@media only screen and (min-width: 768px) {
    .App {
        padding: 20px;
        background-size: cover;
    }

    .AppContainer {
        max-width: 90%;
        margin: 0 auto;
        padding: 20px;
    }

    .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        padding: 20px;
    }

    .dropdown-menu {
        width: 90%;
    }

    .chart {
        width: 100%;
        margin-top: 30px;
    }

    .lcfe_info {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding: 20px;
    }

    .box, .box-2, .box-3 {
        width: 100%;
    }

    .footer {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        padding: 20px;
        text-align: left;
    }

    .social-links ul {
        justify-content: flex-start;
    }
}



@media only screen and (min-width: 1200px) {
    .App {
        padding: 30px;
