        padding: 30px;
    }

    .container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
        padding: 30px;
    }

    .dropdown-menu {
        width: 100%;
    }

    .chart {
        width: 100%;
        margin-top: 40px;
    }

    .lcfe_info {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
        padding: 30px;
    }

    .box, .box-2, .box-3 {
        width: 100%;
    }

    .footer {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
        padding: 30px;
        text-align: left;
    }

    .social-links ul {
        justify-content: flex-start;
    }
