/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #198754;
  padding: 1rem 2rem;
  width: 102%; /* Ensure the navbar spans the full width of the screen */
  box-sizing: border-box;
  position: fixed;
  top: 0;
  z-index: 2000;
  font-weight: bold;
  height: 97px;
  
}

.navbar-brand {
  flex: 1;
}

.navbar-brand img {
 margin-left: 60px;
  width: 240px;
  height: 75px;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 3rem;
  margin: 0;
  padding: 0;
}

.navbar-links a {
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  padding: 9px 16px;
  border: 2px solid transparent;
  transition: background-color 0.3s, color 0.3s, border 0.3s;
  border-radius: 6px;
  box-sizing: border-box;
}

.navbar-links a:hover {
  background-color: white;
  color: #198754;
  border-color: #198754;
}

/* Dropdown Styling */
.dropdown-menu,
.dropdown-menu-2 {
  display: none;
  position: absolute;
  background-color: #fff;
  list-style: none;
  padding: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.dropdown-menu {
  left: 767px;
  top: 64px;
}

.dropdown-menu-2 {
  left: 575px;
  top: 69px;
}

.dropdown-menu li,
.dropdown-menu-2 li {
  margin: 0;
}

.dropdown-menu a,
.dropdown-menu-2 a {
  display: block;
  padding: 10px 20px;
  color: #333;
  text-decoration: none;
  font-size: 14px;
}

.dropdown-menu a:hover,
.dropdown-menu-2 a:hover {
  background-color: #f0f0f0;
}

/* Show dropdown on hover */
.dropdown:hover .dropdown-menu,
.dropdown:hover .dropdown-menu-2 {
  display: block;
}

/* Hamburger Menu Styling */
.hamburger-menu {
  display: none;
  cursor: pointer;
  font-size: 24px;
}

.hamburger-menu .bar {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #fff;
  margin: 5px auto;
  transition: 0.3s;
}

.menu {
 
  display: none;
  flex-direction: column;
  background-color: #198754;
  position: absolute;
  top: 70px; /* Adjust this based on the navbar height */
  right: 1px;
  width: 400px; /* Adjust width as needed */

}
.menu ul{
  list-style: none;

}

.menu.show {
  display: flex;
}

.menu a {
  padding: 12px;
  text-decoration: none;
  color: #fff;
  display: block;
  font-size: 14px;
  font-weight: bold;
}

